





































import {
	mapMutations
} from 'vuex'
export default {
	data() {
		return {
			// 表单数据
			info: {
				msg: "hmc_cici@hotmail.com", // 手机号码
				password: "_hlj12345_" // 密码
			},
			isOpen: false ,// 密码眼睛是不是开着
			redirect:""
		};
	},
	methods: {
		...mapMutations(['login']),
		// 检验输入的数据
		chechInfo() {
			if (!this.$public.reg.mailReg.test(this.info.msg)) {
				uni.showToast({
					icon: "none",
					title: '请输入正确的邮箱地址',
					duration: 2000
				})

				return false
			}

			if (!this.$public.reg.passwordReg.test(this.info.password)) {
				uni.showToast({
					icon: "none",
					title: '请输入6-20位密码',
					duration: 2000
				})

				return false
			}
			return true
		},
		// 登录
		loginFun() {
			if (!this.chechInfo()) {
				return false
			}
			this.$public.API_GET({
				url: "login",
				type:"POST",
				data: {
					userName: this.info.msg,
					password: this.info.password
				},
				success: (res) => {
					if (!res.data.success) {
						uni.showToast({
							icon: 'none',
							title: res.data.message
						});
						return false;
					}
					this.login(res.data);
					uni.setStorageSync("persion",res.data);
					if (this.redirect) {
						uni.reLaunch({
							url: this.redirect
						});							
					} else {
						uni.reLaunch({
							url: '/pages/index/index'
						});
					}
				}
			});
		},
	},
	onLoad(e) {
		this.redirect = e.redirect;
	},
	mounted() {

	}
}
